.videosBox {
    width: 55%;
    background-color: #EDF2F6;
    margin: 65px auto 30px;
    border-radius: 5px;
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.videosHeader{
    background-color: white;
    color: #515365;
    display: block;
    border-left: 3px solid #f5604f;
    padding:  0 20px;
    font-size: 18px;
    border-radius: 5px;
    text-align: left;
}
.videosHeader span{
    padding: 5px;
    border-radius: 100%;
    color: white;
    font-size: 11px;
    background-color: #7750f8;
}
.videos{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color: white;
    border-radius: 5px;
    margin-top: 15px;
    padding: 10px 0;
}
.videoCard a{
    text-decoration: none;
    -moz-transition: all 1s ease-out;
    -o-transition: all 1s ease-out;
    -webkit-transition: all 1s ease-out;
}
.videoCard a:hover{
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -o-transform: scale(1.1);
}
.videoCard img {
    width: 210px;
    position: relative;
    border-radius: 8px;
    overflow: hidden;
    transition: transform 0.25s;
    -o-transition: all 1s ease-out;
    -webkit-transition: all 1s ease-out;
}
.videoCard img :hover{
    /*transition: all 0.2s linear 0s;*/
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -o-transform: scale(1.1);
}
.videoCard{
    /*padding: 10px 5px;*/
    margin: 10px;
    overflow: hidden;
}
.videoCard i{
    position: absolute;
    color: rgba(255, 255, 255, 0.72);
    background-color: rgba(245, 96, 79, 0.61);
    border-radius: 100%;
    top: 24%;
    left: 37%;
    width: 30px;
    height: 30px;
    border: 2px solid rgba(255, 255, 255, 0.75);
    font-size: 30px;
    padding: 10px;
}
