li {
    text-decoration: none;
    list-style: none;
}
a {
    text-decoration: none;
    
} 
.dialogs{
    width: 55%;
    background-color: #fff;
    margin: 75px auto 30px;
    padding: 20px 0;
    border-radius: 7px;
    display: flex;
    flex-direction: row;
    align-content: left;
    height: 100vh;
    font-family: 'Roboto', sans-serif;
}


.dialogsItem a{
    padding: 10px;
    height: 20px;
    text-align: left;
    justify-content: center;
    color:#515365;
    font-size: 12px;
    font-size: 15px;
    text-align: left;
    font-family: 'Roboto', sans-serif;
}
.messages{
    padding: 10px;
    color: #34465d;
    text-align: left;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    height: 20px;
    font-size: 15px;
}
.messageItem{
    /* fdf */
}
.sendBtn{
    background-color: white;
    color: #f5604f;
    padding: 8px 12px;
    border-radius: 12px;
    font-weight: 700;
    font-size: 15px;
    cursor: pointer;
    left: 60px;
    position: relative;
    top: 20px;
    border: 1px solid #f5604f;
}
.sendBtn:hover{
    background-color: #f5604f;
    color: white;
    border: 1px solid #f5604f;
    padding: 8px 12px;
    border-radius: 12px;

}
.inputMessage{
    background-color:#f5604f;
    width: 65%;
    border-radius: 10px;
    border: 1px solid #34465d;
    font-size: 14px;
    height: 40px;
    text-align: end;
    
}
